
.MatNav {
  position: fixed;
  bottom: 0;
  height: 56px;
  background-color: red;
  width: 100%;
}
.MatNav-Container {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.MatNav-Item-Container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
  text-align: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0px;
  list-style-type: none;
  width: 100%;
  max-width: 168px;
  min-width: 80px;
  height: 56px;
  text-decoration: none;
}
.MatNav-Icon {
  color: #ffffff;
  padding: 0;
  margin: 6px;
  display: block;
  text-decoration: none;
  height: 24px;
  width: 24px;
  font-size: 24px;
}
.MatNav-Label {
  position: fixed;
  margin: 0 0 10px 0;
  color: #ffffff;
  bottom: 0;
}
main {
  margin: 0 0 56px 0;
}
@media screen and (min-width: 768px) {
.MatNav {
    padding-top: 56px;
    width: 56px;
    height: 100%;
    left: 0;
    border-left: 1px #efefef;
}
.MatNav-Container {
    margin-top: 56px;
    height: 100%;
    width: 56px;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: unset;
        -ms-flex-pack: unset;
            justify-content: unset;
}
.MatNav-Item-Container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    height: 56px;
    width: 56px;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    text-align: center;
    max-width: unset;
    min-width: unset;
}
.MatNav-Label {
    display: none;
}
.MatNav-Icon {
    margin: auto;
}
main {
    margin: 0 0 0 61px;
}
}
.MatNav {
  background-color: #1F96EC;
}
@media screen and (min-width: 768px) {
.MatNav-Container, .MatNav {
    margin-top: 56px;
}
}
slot {
  margin-top: 56px;
}



