
html, body {
  margin: 0;
	height: 100%;
	width: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
	height: 100%;
	width: 100%;
}
.main {
  text-align: center;
  margin-top: 56px;
	margin-left: 56px;
	height: calc(100% - 56px);
	width: calc(100% - 56px);
}
header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
	width: 100%;
	position: fixed;
	z-index: 2;
  -webkit-box-shadow: 0px 4px 4px rgba(0,0,0, 0.24);
          box-shadow: 0px 4px 4px rgba(0,0,0, 0.24);
	top: 0px;
}
.headershadow{
	-webkit-box-shadow: 0px 0px 4px rgba(0,0,0, 0.12);
	        box-shadow: 0px 0px 4px rgba(0,0,0, 0.12);
  height: 56px;
}
header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 16px;
}



